import { Component, h, Prop } from '@stencil/core';


@Component({
    tag: 'ks-recipe-info',
    styleUrl: 'recipe-info.css'
})
export class RecipeInfo {
    @Prop() time: string;
    @Prop() yield: string;

    @Prop() cuisine: string;
    @Prop() cuisineLink: string = "";

    @Prop() category: string;
    @Prop() categoryLink: string = "";

    @Prop() image: string;

    render() {
        return [
            <div class="info">
                <div class="title">
                    <div class="heading">
                        <slot name="heading"></slot>
                        <div class="categories">
                            {this.link(this.cuisine, this.cuisineLink)}â€¢
                            {this.link(this.category, this.categoryLink)}
                        </div>
                    </div>
                    <div>
                        <div class="time"><ks-icon name="clock"></ks-icon>{this.time}</div>
                        <div class="yield"><ks-icon name="users"></ks-icon>{this.yield}</div>
                    </div>
                </div>
                <div class="summary">
                    <slot name="summary"></slot>
                </div>
                <div class="nutrition">
                    <slot name="nutrition"></slot>
                </div>
            </div>,
            <div class="image">
                <ks-img2 vertical src={this.image}></ks-img2>
            </div>
        ];
    }

    link(name: string, url: string) {
        if(url) return <a href={url}>{name}</a>
        return <span>{name}</span>
    }
}
